.main__skills {
  color: $color-light;
  text-align: center;
  font-family: $font-main;

  @include media($tablet) {
    text-align: right;
    margin-right: 40px;
  }

  &__title {
    font-family: $font-titles;
    font-size: rem(50);
    text-transform: uppercase;
    margin: 0;

    @include media($tablet) {
      font-size: rem(75);
    }
  }

  &__list {
    font-size: rem(25);
    line-height: 2rem;
    text-align: left;
    @include media($tablet) {
      text-align: right;
      font-size: rem(35);
    }

    &__item {
      display: flex;
      flex-direction: row-reverse;
      justify-content: center;
      align-items: flex-start;
      margin-bottom: 5px;

      @include media($tablet) {
        display: block;
        text-align: right;
      }

      &__smiley {
        margin-left: 20px;
        margin-right: 5px;
      }
    }
  }
}
